@use '../../scss/util/' as util;

.tagsList {
    ul{
        display: flex;
        gap:10px;
        flex-wrap: wrap;
        margin-bottom: 1rem;
        li{
            @include util.flex-center;
            align-items: center;
            color:util.$secondary-color;
            background-color:util.$primary-color;
            border-radius: 0.7em;
            font-weight: 500;
            font-size:0.7em;
            min-width:11em;
            padding:0.3em 1em;
            @media (max-width:util.$breakpoint-mobile){
                font-size:0.5em;

            }
        }
    }
}